import React from "react"
import pricing from "./price-content"
import Prc from "./Prc"

export default function Price(){
function generatePrice(p){
return(
    <Prc
    what= { p.what}
    price= {p.price}
    />
)
}

return(
  <div id="price" className="price-container">
    <h1 className="title">Cennik</h1>
<div>
  {pricing.map(generatePrice)}
</div>

  </div>
)
}