import React from "react";
import offerContent from "./offer-content";
import Card from "./Card";

export default function Offer() {
  function generateOffer(ofer) {
    return <Card
    title={ofer.title}
    img={ofer.img}
    content={ofer.content} />;
  }

  return (
    <div id="offer" className="offer-container">
      <div className="block-offer">
        <h1 className="offer-title">Oferta</h1>
      </div>{" "}
      <div className="cards">
          {offerContent.map(generateOffer)}
      </div>
    </div>
  );
}
