import React from "react";

export default function Burger() {
  const slider = () => {
    const burger = document.querySelector(".burger");
    const navlinks = document.querySelector(".nav-links");
    const links = document.querySelectorAll(".nav-links li");
    const line = document.querySelectorAll(".line");
   
    burger.addEventListener("click", () => {
      
      burger.classList.toggle("burger-hidden")
      navlinks.classList.toggle("open");
      links.forEach(link => {
        link.classList.toggle("fade");
      });
      line.forEach(l => {
        l.classList.toggle("white");
      })
    });
  };

  return (
    <nav>
      <div className="burger-navbar">
        <div className="burger burger-hidden" onClick={slider}>
          <div className="one line"></div>
          <div className="two line"></div>
          <div className="three line"></div>
        </div>

        <ul className="nav-links">
          <li>
            <a href="#about">Nasi lekarze</a>
          </li>
          <li>
            <a href="#offer">Oferta</a>
          </li>
          <li>
            <a href="#price">Cennik</a>
          </li>
          <li>
            <a href="#contact">Kontakt</a>
          </li>
        </ul>
      </div>
    </nav>
  );
}
